import React from "react";
import "./Navbar.css";
import gsap from "gsap";
import { useRef, useEffect } from "react";
import { NavLink } from "react-router-dom";

function Navbar() {
  const logRef = new useRef();

  useEffect(() => {
    let el = logRef.current;
    gsap.to(el, { opacity: 1, duration: 0 });
  }, []);

  return (
    <div className="Navbar">
      <div className="logo" id="name" ref={logRef}>
        NIKHIL MEENA
      </div>
      <div className="other">
        <div className="functionalities ">
          <NavLink to="/home" activeClassName="activated">
            Home
          </NavLink>
        </div>

        <div className="functionalities ">
          <NavLink to="/about" activeClassName="activated">
            About
          </NavLink>
        </div>

        <div className="functionalities ">
          <NavLink to="/contact" activeClassName="activated">
            Contact
          </NavLink>
        </div>
      </div>
    </div>
  );
}
export default Navbar;
